import { Controller, Commands } from '@ali/4ever-cangjie';
import { TemplateButton } from '../../mo';
import { SetDataProps } from '../actions';

export function setNodeData(
  controller: Controller,
  payload: { key: string; data: SetDataProps },
) {
  const { key, data } = payload;
  const node = controller.value.document.getNode(key) as TemplateButton;
  controller.command(Commands.setNodeByKey, key, {
    type: node.type,
    data: {
      ...node.data,
      metadata: {
        ...node.data.metadata,
        ...data,
      },
    },
  });
  return controller;
}
